import {memo}  from 'react'
import { Link } from 'react-router-dom'

const ProductTopRated = ({img,id,  name, rating, price}) => {


  return (
    <div className="tp-shop-widget-product-item d-flex align-items-center">
    <div className="tp-shop-widget-product-thumb">
        <Link to={`/product-details/${id}`}>
          {/* <img src={products.length > 0 && products.find(data => data.prod_stat === "R").image} alt=""/> */}
          <img src={img} alt={img}/> 
       </Link>
    </div>
    <div className="tp-shop-widget-product-content">
       <div className="tp-shop-widget-product-rating-wrapper d-flex align-items-center">
          <div className="tp-shop-widget-product-rating">
          
                <span><i className="fa-solid fa-star"></i></span>
                <span><i className={`fa-star fa-${rating >=2 ? 'solid' : 'regular'}`}></i></span>
                <span><i className={`fa-star fa-${rating >=3 ? 'solid' : 'regular'}`}></i></span>
                <span><i className={`fa-star fa-${rating >=4 ? 'solid' : 'regular'}`}></i></span>
                <span><i className={`fa-star fa-${rating >=5 ? 'solid' : 'regular'}`}></i></span>
               
          </div>
          <div className="tp-shop-widget-product-rating-number">
             <span>(4.2)</span>
          </div>
       </div>
       <h4 className="tp-shop-widget-product-title">
          <a href="product-details.html">{name}</a>
       </h4>
       <div className="tp-shop-widget-product-price-wrapper">
          <span className="tp-shop-widget-product-price">${price}</span>
       </div>
    </div>
 </div>
  )
}

export default memo(ProductTopRated)