import { useSelector, useDispatch } from "react-redux"
import { useLocation } from 'react-router-dom'
import { useSearchParams,Link } from "react-router-dom"
import { useState, useEffect } from 'react'
import SearchBox from "./SearchBox"
import { listProducts } from "../redux/actions/productActions"
// import { productListReducerSearch } from "../redux/reducers/productReducers"
import { addSearchProducts } from "../redux/actions/productActions"
import LogoData from './lib/logo.svg'
const dataAPI = '/api/products/genericslist'
const Header = ({openCheck, checkSide}) => {
   const dispatch = useDispatch()
   
   let location = useLocation()
   location.search = ''
 
   const [searchParams, setSearchParams ] = useSearchParams({})
   const [searchData, setSearchData] = useState([])
   const productList = useSelector(state => state.productList)

   //user login state
   const userLogin = useSelector(state => state.userLogin)
   const { userInfo } = userLogin

   const { loading, error, products } = productList

   const cart = useSelector(state => state.cart)
   const { cartItems } = cart
   const wish = useSelector(state => state.wishList)
   const { wishListItems } = wish
 
   const handlesubmit = (e) => {
      e.preventDefault()
      
      const formData = new FormData(e.target);
      let datax = Object.fromEntries(formData);
      
      const orderDetails = {
      ...Object.fromEntries(formData)
      };
      // setSearchParams({ product: orderDetails.search_input, category: orderDetails.select_cat });
      setSearchParams({ product: orderDetails.search_input});
 
      const values = searchParams.entries() //entries will help to iterate
      searchParams.search && searchParams.search.delete("product")
 


      // Create a regex object (a constructor).
      let regex = new RegExp(orderDetails.search_input, 'i'); 	
      let regex_cat = new RegExp(orderDetails.select_cat, 'i'); 	
      // Tests for a match.
      if(products.results!==undefined){
         const ee = products.results.filter(item => regex.test(item.name));
     
         console.log('eeeee',ee)
         dispatch(addSearchProducts(ee, true))
  
      }

      e.target.reset()
   }
   
  return (
    <header>
         <div className="tp-header-area p-relative z-index-11">
            {/* <!-- header top start  --> */}
            <div className="tp-header-top black-bg p-relative z-index-1 d-none d-md-block">
               <div className="container">
               
               </div>
            </div>

            {/* <!-- header main start --> */}
            <div className="tp-header-main tp-header-sticky">
               <div className="container">
                  <div className="row align-items-center">
                     <div className="col-xl-2 col-lg-2 col-md-4 col-6">
                        <div className="logo">
                           <Link to="/">
                              <img src={LogoData} alt={LogoData}/>
                           </Link>
                          
                        </div>
                     </div>
                     <div className="col-xl-6 col-lg-7 d-none d-lg-block">
                        <div className="tp-header-search pl-70">
                        {/* {location.pathname === "/" && <SearchBox/> || location.pathname === "/shop" && <SearchBox/> } */}
                       { location.pathname === "/" || location.pathname === "/shop" ? <form onSubmit={handlesubmit}>
                           <div className="tp-header-search-wrapper d-flex align-items-center">
                           <div className="tp-header-search-box">
                                 <input type="text" placeholder="Search for Products..."  name="search_input" required />
                           </div>
                           {/* <div className="tp-header-search-category">
                                 <select className="form-select" name="select_cat">
                                    <option>Select Category</option>
                              
                                    {
                                       categories.length > 0 && categories !== undefined &&
                                       categories.map((data)=> data.prod_num > 0 ? <option key={data.id}>{data.name}</option> : null )
                                    }
                                 </select>
                           </div> */}
                           <div className="tp-header-search-btn">
                                 <button type="submit">
                                 <i className="fa-regular fa-magnifying-glass" style={{color: "#ffffff"}}></i>
                                
                                 </button>
                           </div>
                           </div>
                     </form>
                     : <div></div>   
                  }
                        </div>
                     </div>
                     <div className="col-xl-4 col-lg-3 col-md-8 col-6">
                        <div className="tp-header-main-right d-flex align-items-center justify-content-end">
                           <div className="tp-header-login d-none d-lg-block">
                              <Link to={ userInfo ? "/profile" : "/login"} className="d-flex align-items-center">
                             
                                 <div className="tp-header-login-icon">
                                 <span>
                                 <i className="fa-light fa-user"></i>
                                 </span>
                               
                                 </div>
                                 <div className="tp-header-login-content d-none d-xl-block">
                                    <span>Hello, {userInfo ? userInfo.name : "Sign In"}</span>
                                    <h5 className="tp-header-login-title">Your Account</h5>
                                 </div>
                         
                              </Link>
                           </div>
                           <div className="tp-header-action d-flex align-items-center ml-50">
                             
                              <div className="tp-header-action-item d-none d-lg-block">
                         
                                 <Link className="tp-header-action-btn" to={`/wish-list`}>
                                 <i className="fa-light fa-star"></i>
                                    
                                   
                                    {wishListItems.length > 0 && <span className="tp-header-action-badge">{wishListItems.length}</span>}      
                                 </Link>
                                                     
                           
                              </div>
                              <div className="tp-header-action-item">
                                 <button type="button" className="tp-header-action-btn cartmini-open-btn" onClick={openCheck}>
                                 <i className="fa-light fa-cart-shopping"></i>
                                    {/* <span className="tp-header-action-badge">13</span>                                                                           */}
                                    { cartItems.length > 0 && <span className="tp-header-action-badge">{cartItems.length}</span>}
                                 </button>
                              </div>
                              <div className="tp-header-action-item d-lg-none">
                                 <button type="button"  className="tp-header-action-btn tp-offcanvas-open-btn" 
                                 onClick={checkSide}
                                 >
                                 {/* onClick={openCheck} */}
                                    <svg xmlns="http://www.w3.org/2000/svg" width="30" height="16" viewBox="0 0 30 16">
                                       <rect x="10" width="20" height="2" fill="currentColor"/>
                                       <rect x="5" y="7" width="25" height="2" fill="currentColor"/>
                                       <rect x="10" y="14" width="20" height="2" fill="currentColor"/>
                                    </svg>
                                 </button>
                              </div>
                              
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>

            {/* <!-- header bottom start --> */}
            <div className="tp-header-bottom tp-header-bottom-border d-none d-lg-block">
               <div className="container">
                  <div className="tp-mega-menu-wrapper p-relative">
                     <div className="row align-items-center justify-content-center">
                    
                        <div className="col-xl-6 col-lg-6">
                           <div className="main-menu menu-style-1">
                              <nav className="tp-main-menu-content">
                                 <ul>
                                    <li>
                                  
                                       <Link to="/" onClick={()=> dispatch(addSearchProducts([], false))}>Home</Link>
                                    </li>
                                    <li >
                                    <Link to="/shop" onClick={()=> dispatch(addSearchProducts([], false))}>Shop</Link>
                                      
                                   </li>
                                    <li >
                                       
                                   
                                       <Link to="/shop">
                                       Products
                                       </Link>
                                    </li>
                                    <li>
                                    
                                       <Link to="/">
                                       Categories
                                       </Link>
                                    </li>
                                  
                                    <li>
                                    <Link to="/contact">
                                       Contact
                                       </Link>
                                    </li>
                                 </ul>
                              </nav>
                           </div>
                        </div>
                        <div className="col-xl-3 col-lg-3">
                           <div className="tp-header-contact d-flex align-items-center justify-content-end">
                              <div className="tp-header-contact-icon">
                                 <span>
                                 <i className="fa-light fa-phone fa-xl"></i>
                                 </span>
                              </div>
                              <div className="tp-header-contact-content">
                                 <h5>Hotline:</h5>
                                 <p><a href="tel:402-763-282-46">+(402) 763 282 46</a></p>
                              </div>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </div>
      </header>
  )
}

export default Header