
import { useDispatch, useSelector  } from "react-redux";
import { useState, useEffect } from 'react'
import { useNavigate } from "react-router-dom";
import { updateUserProfile } from "../../../redux/actions/userActions";

const Information = () => {
   const [checkSuccess, setCheckSuccess ] = useState(false)
   const [name, setName] = useState('')
   const [email, setEmail] = useState('')
   const [password, setPassword] = useState('')
   const [confirmPassword, setConfirmPassword] = useState('')
   const [message, setMessage] = useState('')
   const dispatch = useDispatch()
   const userLogin = useSelector(state => state.userLogin)
   const { userInfo } = userLogin
   const userUpdateProfile = useSelector(state => state.userUpdateProfile)
   const { success } = userUpdateProfile

   const navigate = useNavigate()

   useEffect(() => {
      if (!userInfo) {
         navigate('/login')
      } else {
          if (!userInfo || !userInfo.name || userInfo._id) {
         
  
              setName(userInfo.name)
              setEmail(userInfo.email)
          }
      }
  }, [dispatch, userInfo, navigate])

   const updateProfile = (e) =>{
      e.preventDefault()
      if (password != confirmPassword) {
         setMessage('Passwords do not match')
     } else {
         dispatch(updateUserProfile({
             'id': userInfo._id,
             'name': name,
             'email': email,
             'password': password
         }))
         setMessage('')
         setCheckSuccess(true)
         setTimeout(()=> {
            setCheckSuccess(false)
        }, 3000)
     }
     
   }
  return (
    <div >
    <div className="profile__info">
       <h3 className="profile__info-title">Personal Details</h3>
       <div className="profile__info-content">
          <form onSubmit={updateProfile}>
          { 
          checkSuccess &&
                  
                  <div className="row">
               <div className="col-xxl-12 col-md-12 text-center">
               <div className="alert alert-success fw-bold">Successfully updated!</div>
               </div>
               </div>
               }
             <div className="row">
             <div className="col-xxl-6 col-md-6">
                    <div className="tp-profile-input-box">
                    <div className="tp-profile-input">
                        <input name="username" id="username" type="text" required
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        />
                    </div>
                    <div className="tp-profile-input-title">
                        <label htmlFor="username">Name</label>
                    </div>
                    </div>
               </div>
               <div className="col-xxl-6 col-md-6">
                  <div className="tp-profile-input-box">
                  <div className="tp-profile-input">
                     <input name="email" id="con_new_pass" type="email" required
                     value={email}
                     onChange={(e) => setEmail(e.target.value)}
                     />
                  </div>
                  <div className="tp-profile-input-title">
                     <label htmlFor="email">Email</label>
                  </div>
                  </div>
               </div>
               {
                  message && 
                  <div className="row">
               <div className="col-xxl-12 col-md-6">
               <div className="alert alert-danger">{message}</div>
               </div>
               </div>
               }
               <div className="col-xxl-6 col-md-6">
                    
                    <div className="tp-profile-input-box">
                    <div className="tp-profile-input">
                        <input name="new_pass" id="new_pass" type="password" 
                        
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        />
                    </div>
                    <div className="tp-profile-input-title">
                        <label htmlFor="new_pass">Password</label>
                    </div>
                    </div>
                </div>
                <div className="col-xxl-6 col-md-6">
                    <div className="tp-profile-input-box">
                    <div className="tp-profile-input">
                        <input name="con_new_pass" id="con_new_pass" type="password" 
                        value={confirmPassword}
                        onChange={(e) => setConfirmPassword(e.target.value)}
                        />
                    </div>
                    <div className="tp-profile-input-title">
                        <label htmlFor="con_new_pass">Confirm Password</label>
                    </div>
                    </div>
                </div>
               

                <div className="col-xxl-12">
                   <div className="profile__btn">
                      <button type="submit" className="tp-btn">Update Profile</button>
                   </div>
                </div>
             </div>
          </form>
       </div>
    </div>
 </div>
  )
}

export default Information