import { useState } from "react";
import { useDispatch } from "react-redux";
import { addToCartData } from "../../redux/actions/cartActions";
import { addWishList } from "../../redux/actions/wishListActions";
import { Link } from "react-router-dom";
import Toast from "../Toast";

const ShopProductCard = ({id,stat, cat, productname, numReview, price, img, rating, countInStock}) => {
    const [toasted, setToasted] = useState(null)
    const [stats, setStats] = useState(null)
    const dispatch = useDispatch()
    const addToCarts = (id, name, img, price, countInStock, qty) => {

            dispatch(addToCartData(id, name, img, price, countInStock, qty))
        
        
    }
    const addtoWish = (id, name, img, price, countInStock, qty) => {
      
            dispatch(addWishList(id, name, img, price, countInStock, qty))
        
       
    }
    const showCart = (id,st) => {
        
        setToasted(id)
        setStats(st)
        setTimeout(()=> {
            setToasted(null)
            setStats(null)
        }, 2000)
    }

  return (
    <div className="col-xl-4 col-md-6 col-sm-6 infinite-item">
        <div className="tp-product-item-2 mb-40">
            <div className="tp-product-thumb-2 p-relative z-index-1 fix w-img">
                <Link to={`/product-details/${id}`}>
                <img src={img} alt=""/>
                </Link>
                {/* <!-- product action --> */}
                <div className="tp-product-action-2 tp-product-action-blackStyle">
                <div className="tp-product-action-item-2 d-flex flex-column">
                    <button type="button" className="tp-product-action-btn-2 tp-product-add-cart-btn"
                    onClick={()=> addToCarts(id, productname, img,price, countInStock, 1)}
                    >
                    <i className="fa-light fa-cart-shopping"></i>
                        
                        <span className="tp-product-tooltip tp-product-tooltip-right">Add to Cart</span>
                    </button>
                    <button type="button" className="tp-product-action-btn-2 tp-product-quick-view-btn" data-bs-toggle="modal" data-bs-target="#producQuickViewModal">
                    <i className="fa-sharp fa-regular fa-eye"></i>
                        <span className="tp-product-tooltip tp-product-tooltip-right">Quick View</span>
                    </button>
                    <button type="button" className="tp-product-action-btn-2 tp-product-add-to-wishlist-btn"
                    onClick={()=>{ 
                        showCart(id, "addtowish");
                        addtoWish(id, productname, img,price, countInStock, 1);
                        
                    }}
                    >
                    <i className="fa-light fa-star"></i>
                        <span className="tp-product-tooltip tp-product-tooltip-right">Add To Wishlist</span>
                    </button>
                    
                </div>
                </div>
            </div>
            <div className="tp-product-content-2 pt-15">
                <div className="tp-product-category">
                    {/* <Toast message={"Out of Stock!"} color={"#d41a1a"} /> */}
                {toasted === id && stats=== "addtocart" && countInStock > 0 && <Toast message={"Added to Cart!"} color={"#0992fb"}/>}
                {toasted === id && stats=== "addtowish" && countInStock > 0 && <Toast message={"Added to WishList!"} color={"#65e970"}/>}
                {toasted === id && stats=== "addtowish" && countInStock ===0 && <Toast message={"Out of Stock!"} color={"#d41a1a"} />}
                {id && toasted === id && stats=== "addtocart" && countInStock ===0 && <Toast message={"Out of Stock!"} color={"#d41a1a"} />}
                    <a href="shop.html">{cat}</a>
                </div>
                <div className="tp-product-tag-2">
                <a href="#">Whitetails Store</a>
                </div>
                
                <h3 className="tp-product-title-2">
                {/* <a href="product-details.html">Whitetails Womens Open Sky</a> */}
                {productname}
                </h3>
                <div className="tp-product-rating-icon tp-product-rating-icon-2">
                <span><i className="fa-solid fa-star"></i></span>
                <span><i className={`fa-star fa-${rating >=2 ? 'solid' : 'regular'}`}></i></span>
                <span><i className={`fa-star fa-${rating >=3 ? 'solid' : 'regular'}`}></i></span>
                <span><i className={`fa-star fa-${rating >=4 ? 'solid' : 'regular'}`}></i></span>
                <span><i className={`fa-star fa-${rating >=5 ? 'solid' : 'regular'}`}></i></span>
                </div>
                <div className="tp-product-price-wrapper-2">
                {/* <span className="tp-product-price-2 new-price">$340.00</span> */}
                <span className="tp-product-price-2 new-price">${price}</span>
                </div>
            </div>
        </div>
    </div>
  )
}

export default ShopProductCard