import { useState, useEffect } from "react"
import { useNavigate } from "react-router-dom"
import { useSelector, useDispatch } from "react-redux"
import Address from "./subprof/Address"
import ChangePassword from "./subprof/ChangePassword"

import MainInfo from "./subprof/MainInfo"
import MyOrders from "./subprof/MyOrders"
import Notification from "./subprof/Notification"
import Information from "./subprof/Information"
import { listMyOrders } from "../../redux/actions/orderActions"
const Profile = () => {
   const navigate = useNavigate()
   const dispatch = useDispatch()
   const [currentNav, setCurrentNav] = useState('profile')
   const [prev, setPrev ] = useState('.nav-link.nav.profile')
   const orderListMy = useSelector(state => state.orderListMy)
    const { loading: loadingOrders, error: errorOrders, orders } = orderListMy

   const userLogin = useSelector(state => state.userLogin)
   const { userInfo } = userLogin
   useEffect(()=>{
      if(!userInfo){
         navigate("/login")
      } 
      else {
         dispatch(listMyOrders())
      }
   },[userInfo, navigate, dispatch])
   const checkData = (data) => {
      setCurrentNav(data)
      // cC = (data)
   }
   const rmv = (data) => document.querySelector(data).classList.remove('active')
   const addData = (data) => document.querySelector(data).classList.add('active')
   const checkActive = (data) => document.querySelector(data).classList.contains('active')
   const cC = (data) => {
   
       if(
           (data === '.nav-link.nav.profile' && checkActive(data)) ||
           (data === '.nav-link.nav.information' && checkActive(data)) ||
         //   (data === '.nav-link.nav.address' && checkActive(data)) || 
           (data === '.nav-link.nav.myorders' && checkActive(data)) || 
           (data === '.nav-link.nav.notification' && checkActive(data)) 
         //   ||
         //   (data === '.nav-link.nav.changepassword' && checkActive(data)) 
           ){
     
           return
       }
       if(
           (data !== '.nav-link.nav.profile' && checkActive(prev)) ||
           (data !== '.nav-link.nav.information' && checkActive(prev)) ||
         //   (data !== '.nav-link.nav.address' && checkActive(prev)) ||
           (data !== '.nav-link.nav.myorders' && checkActive(prev)) || 
           (data !== '.nav-link.nav.notification' && checkActive(prev)) 
         //   || 
         //   (data !== '.nav-link.nav.changepassword' && checkActive(prev)) 
           ){
           rmv(prev)
         
       }
       if(
           (data === '.nav-link.nav.profile' && !checkActive(data)) ||
           (data === '.nav-link.nav.information' && !checkActive(data)) ||
         //   (data === '.nav-link.nav.address' && !checkActive(data))||
           (data === '.nav-link.nav.myorders' && !checkActive(data))||
           (data === '.nav-link.nav.notification' && !checkActive(data)) 
         //   ||
         //   (data === '.nav-link.nav.changepassword' && !checkActive(data))
           ){
           
           addData(data)
       }
       setPrev(data)
   }  

  return (
    <section className="profile__area pt-120 pb-120">
    <div className="container">
       <div className="profile__inner p-relative">
          {/* <div className="profile__shape">
             <img className="profile__shape-1" src="assets/img/login/laptop.png" alt="" />
             <img className="profile__shape-2" src="assets/img/login/man.png" alt="" />
             <img className="profile__shape-3" src="assets/img/login/shape-1.png" alt="" />
             <img className="profile__shape-4" src="assets/img/login/shape-2.png" alt="" />
             <img className="profile__shape-5" src="assets/img/login/shape-3.png" alt="" />
             <img className="profile__shape-6" src="assets/img/login/shape-4.png" alt="" />
          </div> */}
          <div className="row">
             <div className="col-xxl-4 col-lg-4">
                <div className="profile__tab mr-40">
                   <nav>
                      <div className="nav nav-tabs tp-tab-menu flex-column" id="profile-tab" role="tablist">
                        <button onClick={()=> {
                           checkData('profile');
                           cC('.nav-link.nav.profile');
                        }} className="nav-link nav profile active" id="nav-profile-tab" data-bs-toggle="tab" type="button" role="tab" aria-controls="nav-profile" aria-selected="false">
                           <span>
                              <i className="fa-light fa-user"></i>
                           </span>Profile
                        </button>
                        <button onClick={()=> {
                           checkData('information');
                           cC('.nav-link.nav.information');
                        }} className="nav-link nav information" id="nav-information-tab" data-bs-toggle="tab" data-bs-target="#nav-information" type="button" role="tab" aria-controls="nav-information" aria-selected="false">
                           <span>
                              <i className="fa-light fa-circle-info"></i>
                           </span> Information
                        </button>
                        {/* <button className="nav-link nav address" onClick={()=> {
                           checkData('address');
                           cC('.nav-link.nav.address');
                        }} id="nav-address-tab" data-bs-toggle="tab" type="button" role="tab" aria-controls="nav-address" aria-selected="false">
                           <span>
                              <i className="fa-light fa-location-dot"></i>
                           </span> Address
                        </button> */}
                         <button className="nav-link nav myorders" onClick={()=> {
                           checkData('myorders');
                           cC('.nav-link.nav.myorders');
                         }} id="nav-order-tab" data-bs-toggle="tab" type="button" role="tab" aria-controls="nav-order" aria-selected="false">
                           <span>
                              <i className="fa-light fa-clipboard-list-check"></i>
                           </span> 
                           My Orders 
                        </button>
                         {/* <button className="nav-link nav notification" onClick={()=> {
                           checkData('notification');
                           cC('.nav-link.nav.notification');
                         }} id="nav-notification-tab" data-bs-toggle="tab"  type="button" role="tab" aria-controls="nav-notification" aria-selected="false">
                           <span>
                              <i className="fa-regular fa-bell"></i>
                           </span> Notification
                        </button> */}
                         {/* <button 
                         onClick={()=> {
                           checkData('changepassword');
                           cC('.nav-link.nav.changepassword');
                         }}
                         className="nav-link nav changepassword" id="nav-password-tab" data-bs-toggle="tab"  type="button" role="tab" aria-controls="nav-password" aria-selected="false">
                           <span>
                              <i className="fa-light fa-lock"></i>
                           </span> Change Password
                        </button>
                         <span id="marker-vertical" className="tp-tab-line d-none d-sm-inline-block"></span> */}
                      </div>
                   </nav>
                </div>
             </div>
             <div className="col-xxl-8 col-lg-8">
                <div className="profile__tab-content">
                   <div className="tab-content" id="profile-tabContent">
                     {currentNav ==='profile' && <MainInfo userInfo={userInfo}/>}
                     {currentNav ==='information' && <Information/>}
                     {/* {currentNav ==='changepassword' && <ChangePassword/>} */}
                     {currentNav ==='address' && <Address/>}
                     {currentNav ==='myorders' && <MyOrders/>}
                     {currentNav ==='notification' && <Notification/>}
                     {/* <ChangePassword/>
                     <Address/>
                     <MyOrders/>
                      <Notification/> */}
                   </div>
                </div>
             </div>
          </div>
       </div>
    </div>
</section>
  )
}

export default Profile