import { Link } from "react-router-dom"
import { useDispatch, useSelector } from "react-redux"
import { addWishList, removeFromWishList } from "../../redux/actions/wishListActions";
import { addToCartData } from "../../redux/actions/cartActions";
const WishList = () => {
    const wishList = useSelector(state => state.wishList)
    const dispatch = useDispatch()
    const { wishListItems } = wishList
    
    const removeWishList = (id) => {
        dispatch(removeFromWishList(id))
    }
    const addToCarts = (id, name, img, price, countInStock, qty) => {
        dispatch(addToCartData(id, name, img, price, countInStock, qty))
    }
    return ( 
        <>
        <section className="breadcrumb__area include-bg pt-95 pb-50">
            <div className="container">
               <div className="row">
                  <div className="col-xxl-12">
                     <div className="breadcrumb__content p-relative z-index-1">
                        <h3 className="breadcrumb__title">Wishlist</h3>
                        <div className="breadcrumb__list">
                           <span>
                            <Link to='/'>
                            Home
                            </Link>
                           </span>
                           <span>Wishlist</span>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>

         <section className="tp-cart-area pb-120">
            <div className="container">
               <div className="row">
                  <div className="col-xl-12">
                     <div className="tp-cart-list mb-45 mr-30">
                        <table className="table">
                           <thead>
                             <tr>
                               <th colSpan="2" className="tp-cart-header-product">Product</th>
                               <th className="tp-cart-header-price">Price</th>
                               <th className="tp-cart-header-quantity">Quantity</th>
                               <th>Action</th>
                               <th></th>
                             </tr>
                           </thead>
                           <tbody>

                                {
                                    wishListItems.map( (data) => (
                                        <tr key={data.product_id}>
                             
                                        <td className="tp-cart-img"><a href="product-details.html"> <img src={data.image} alt={data.image}/></a></td>
                                     
                                        <td className="tp-cart-title"><a href="product-details.html">
                                            {data.name}</a></td>
                                      
                                        <td className="tp-cart-price"><span>${data.price}</span></td>
                                       
                                        <td className="tp-cart-quantity">
                                           <div className="tp-product-quantity mt-10 mb-10">
                                           <select className="form-select" aria-label="Default select example"
                                          value={data.qty}
                                          
                                          onChange={(e)=> 
                                             dispatch(addWishList(data.product_id, data.name, data.image, data.price, data.countInStock, Number(e.target.value)))
                                          }
                                       
                                       >
                                      
                                       {

                                          [...Array(data.countInStock).keys()].map((x) => (
                                             <option key={x + 1} value={x + 1}>
                                                {x + 1}
                                             </option>
                                          ))
                                       }
                                        </select>
                                           </div>
                                        </td>
       
                                        <td className="tp-cart-add-to-cart">
                                           <button type="submit" className="tp-btn tp-btn-2 tp-btn-blue"
                                           onClick={()=> addToCarts(data.product_id, data.name, data.image,data.price, data.countInStock, data.qty)}
                                           >Add To Cart</button>
                                        </td>
                                        
                                    
                                        <td className="tp-cart-action">
                                           <button className="tp-cart-action-btn"
                                           onClick={()=> removeWishList(data.product_id)}
                                           >
                                            <i className="fa-solid fa-xmark"></i>
                                              <span>Remove</span>
                                           </button>
                                        </td>
                                     </tr>
                                        
                                    ))
                                }
                             
                             
                           </tbody>
                         </table>
                     </div>
                     <div className="tp-cart-bottom">
                        <div className="row align-items-end">
                           <div className="col-xl-6 col-md-4">
                              <div className="tp-cart-update">
                                 <a href="cart.html" className="tp-cart-update-btn">Go To Cart</a>
                              </div>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>
        </>
     );
}
 
export default WishList;