import { Link } from "react-router-dom"

const OffCanvass = ({refChecker,closeBtn }) => {
  return (
   // offcanvas-opened refChecker
  
   <div className="offcanvas__area " ref={refChecker}>
   <div className="offcanvas__wrapper">
      <div className="offcanvas__close">
         <button className="offcanvas__close-btn offcanvas-close-btn" onClick={closeBtn}>
         <i className="fal fa-times"></i>
         </button>
      </div>
      <div className="offcanvas__content">
         <div className="offcanvas__top mb-70 d-flex justify-content-between align-items-center">
            <div className="offcanvas__logo logo">
               <a href="index.html">
                  {/* <img src="assets/img/logo/logo.svg" alt="logo"/> */}
               </a>
            </div>
         </div>
         <div className="tp-main-menu-mobile fix mb-40">
            {/* <button className="tp-offcanvas-category-toggle">
               <i className="fa-solid fa-bars"></i>
               Home
            </button> */}
           
           <div className="list-group list-group-flush fs-6">
            <Link to="/" className="list-group-item list-group-item-action " aria-current="true">
            Home
            </Link>
            <Link to="/shop" className="list-group-item list-group-item-action " aria-current="true">
            Shop
            </Link>
            <Link to="/profile" className="list-group-item list-group-item-action " aria-current="true">
            Profile
            </Link>
    

         </div>
         </div>
        

         <div className="offcanvas__contact align-items-center d-none">
            <div className="offcanvas__contact-icon mr-20">
               <span>
                  {/* <img src="assets/img/icon/contact.png" alt=""/> */}
               </span>
            </div>
            <div className="offcanvas__contact-content">
               <h3 className="offcanvas__contact-title">
                  <a href="tel:098-852-987">004524865</a>
               </h3>
            </div>
         </div>
     
      </div>
    
   </div>
</div>
  )
}

export default OffCanvass