import { useState,useEffect } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from "react-redux"
import { savePaymentMethod } from "../redux/actions/cartActions"
import CheckOutSteps from "../components/CheckOutSteps"

import PayP from '../components/lib/Paypal-logo.png'
const Payment = () => {

    const [paymentMethod, setPaymentMethod] = useState('PayPal')
    const navigate = useNavigate()
   
    const cart = useSelector(state => state.cart)
    const { shippingAddress } = cart

    const dispatch = useDispatch()
    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin
    useEffect(()=>{
        if(!userInfo){
            navigate("/login")
        }
        if(!shippingAddress.address){
            navigate("/shipping")
 
        }
    },[shippingAddress, navigate])

    const handleSubmit = (e) =>{
        e.preventDefault()
        dispatch(savePaymentMethod(paymentMethod))
        navigate("/place-order")
    }
  return (
//     <div className="profile__address">
//     <div className="row justify-content-center align-items-center">
//        <div className="col-md-7">
//           <div className="profile__address-item d-sm-flex align-items-start">
//              <div className="profile__address-icon">
//                 <span>
                   
//                 </span>
//              </div>
//              <div className="profile__address-content">
//                 <h3 className="profile__address-title">Select Method</h3>
                
//                 <div className="form-check">
//                 <input className="form-check-input" checked type="radio" name="flexRadioDefault" id="flexRadioDefault1"/>
//                 <label className="form-check-label" htmlFor="flexRadioDefault1">
//                     PayPal
//                 </label>
//                 </div>
//              </div>
//           </div>
//        </div>
     
//     </div>
//  </div>
<section className="tp-contact-area pb-100">
        <div className="container mx-auto">
        <div className="tp-contact-inner">
            <div className="row justify-content-center">

                <div className="col-xl-8 col-lg-7">
                    <CheckOutSteps step1 step2 step3/>
                    <div className="tp-contact-wrapper">
                    <h3 className="tp-contact-title">Select Method</h3>

                    <div className="tp-contact-form">
                        <form onSubmit={handleSubmit}>
                            <div className="tp-contact-input-wrapper">
                                {
                                    !paymentMethod && (
                                        <div className='alert alert-danger'>Please Select a Payment Method</div>
                                    )
                                }
                            {/* <div className="form-check d-flex align-items-center">
                <input className="form-check-input" checked type="radio" name="flexRadioDefault" id="flexRadioDefault1"
                value={paymentMethod}
                onChange={(e)=> setPaymentMethod(e.target.value)}
                />
          
                 <label>
                 <img className='img-fluid mx-2' style={{maxWidth: "85px"}} src={PayP} alt={PayP}/>
                 </label>
               </div> */}
               <div className="tp-checkout-payment-item">
                    <input type="radio" id="paypal" name="payment"
                    value={paymentMethod}
                    checked
                    onChange={(e)=> setPaymentMethod(e.target.value)}
            
                    />
                    {/* <label htmlFor="cod">Cash on Delivery</label> */}
                    <label htmlFor="paypal">
                    <img className='img-fluid mx-2' style={{maxWidth: "85px"}} src={PayP} alt={PayP}/>
                    </label>
                             
                </div>
                {/* <div className="tp-checkout-payment-item">
                              <input type="radio" id="cod" name="payment"
                              onChange={(e)=> setPaymentMethod(e.target.value)}
                              />
                              <label htmlFor="cod">Cash on Delivery</label>
                             
                           </div> */}
                            </div>
                          
                            {
                                paymentMethod && (
                                    <div className="tp-contact-btn mt-2">
                                        <Link to="/place-order">
                                            Continue
                                        </Link>
                                        {/* <button className="disabled" type="submit" disabled></button> */}
                                    </div>
                                )
                            }
                            
                        </form>
                        <p className="ajax-response"></p>
                    </div>
                    </div>
                </div>
               
            </div>
        </div>
        </div>
    </section>
  )
}

export default Payment