import { useRef } from 'react'
import { HashRouter, Route, Routes} from 'react-router-dom'
import Home from "./components/Home";
import CartPage from './components/cart/CartPage';
import Footer from './components/main/Footer';
import BackArrow from './components/BackArrow';
import OffCanvass from './components/OffCanvass';
import MobileMenuSticky from './components/MobileMenuSticky';
import SearchArea from './components/SearchArea';
import CartMini from './components/CartMini';
import Header from './components/Header';
import WishList from './components/cart/WishList';
import Shop from './components/shop/Shop';
import ProductDetails from './components/shop/ProductDetails';
import Profile from './components/profile/Profile';
import Login from './components/loginregister/Login';
import Register from './components/loginregister/Register';
import Shipping from './screens/Shipping';
import Payment from './screens/Payment';
import PlaceOrder from './screens/PlaceOrder';
import OrderScreen from './screens/OrderScreen';
import { PayPalScriptProvider } from "@paypal/react-paypal-js";
import Contact from './components/Contact';
// import './css/bootstrap.css'
import './App.css'
import './css/bootstrap.css'

// import './css/slick.css'
// import './magnific-popup.css'
// import './css/font-awesome-pro.css'
// import './css/flaticon_shofy.css'
// import './css/spacing.css'

function App() {
  const checks = useRef()
  const checkSideMenu = useRef()
  const checkSearchRef = useRef()

  const client_id = 'ASJdIbuDns4rkTfY8ALxZen1Axg2xe-H_TdjpztHtTyvJ_DBILyxwcgdSjFuVXMsiN5TyqSRrVLuC49r'
  const checkSide = () => {
    console.log('class,',checkSideMenu.current.classList.contains("offcanvas__area"))
    if (!checkSideMenu.current.classList.contains("offcanvas-opened")){
      checkSideMenu.current.classList.add("offcanvas-opened")
    } else{
      checkSideMenu.current.classList.remove("offcanvas-opened")
    }
  }
  const checkC = () => {
    console.log('class,',checks.current.classList.contains("cartmini__area"))
    if (!checks.current.classList.contains("cartmini-opened")){
      checks.current.classList.add("cartmini-opened")
    } else{
      checks.current.classList.remove("cartmini-opened")
    }
  }
  const checkSearch = () => {
    console.log('class,',checkSearchRef.current.classList.contains("tp-search-area"))
    if (!checkSearchRef.current.classList.contains("opened")){
      checkSearchRef.current.classList.add("opened")
    } else{
      checkSearchRef.current.classList.remove("opened")
    }
  }
  //tp-search-area opened
  return (
    <PayPalScriptProvider options={{ "client-id": client_id }}>
      <HashRouter>
        <BackArrow/>
        <OffCanvass refChecker={checkSideMenu} closeBtn={checkSide} />
        <MobileMenuSticky closeBtnSearch={checkSearch}/>
        <SearchArea refSearch={checkSearchRef} closeBtnSearch={checkSearch}/>

        <CartMini refCheck={checks} checkBtn={checkC} />

        <Header openCheck={checkC} checkSide={checkSide}/>
        <Routes>
          <Route path="/" element={<Home/>}/>
          <Route path="/cart" element={<CartPage/>}/>
          <Route path="/wish-list" element={<WishList/>}/>
          <Route path="/shop" element={<Shop/>}/>
          <Route path="/profile" element={<Profile/>}/>
          <Route path="/login" element={<Login/>}/>
          <Route path="/register" element={<Register/>}/>
          <Route path="/shipping" element={<Shipping/>}/>
          <Route path="/payment" element={<Payment/>}/>
          <Route path="/place-order" element={<PlaceOrder/>}/>
          <Route path="/order/:orderId" element={<OrderScreen/>}/>
          <Route path="/product-details/:productid" element={<ProductDetails/>}/>
          <Route path="/contact" element={<Contact/>}/>
        </Routes>
        <Footer/>
      </HashRouter>
      </PayPalScriptProvider>

  );
}

export default App;
