
import { useDispatch, useSelector } from "react-redux";
import { logout } from "../../../redux/actions/userActions";
import { Link, useNavigate } from "react-router-dom";
import  defaultProf  from '../../lib/blank_prof.png'
const MainInfo = ({userInfo}) => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const orderListMy = useSelector(state => state.orderListMy)
    const { loading, error, orders } = orderListMy
    const wishList = useSelector(state => state.wishList)
    const { wishListItems } = wishList
    const logoutHandler = () => {
        dispatch(logout())
        navigate('/login')
    }

          
  return (
    <div className="tab-pane fade show active" id="nav-profile" role="tabpanel" aria-labelledby="nav-profile-tab">
        <div className="profile__main">
        <div className="profile__main-top pb-80">
            <div className="row align-items-center">
                <div className="col-md-6">
                    <div className="profile__main-inner d-flex flex-wrap align-items-center">
                    <div className="profile__main-thumb">
                        <img src={defaultProf} alt={defaultProf}/>
                 
                        {/* <div className="profile__main-thumb-edit">
                            <input id="profile-thumb-input" className="profile-img-popup" type="file"/>
                            <label htmlFor="profile-thumb-input"><i className="fa-light fa-camera"></i></label>
                        </div> */}
                    </div>
                    <div className="profile__main-content">
                        <h4 className="profile__main-title">Welcome {userInfo.name}!</h4>
                        
                    </div>
                    </div>
                </div>
                <div className="col-md-6">
                    <div className="profile__main-logout text-sm-end">
                    {/* userinfo */}
                    <button onClick={logoutHandler} className="tp-logout-btn">Logout</button>
                  
                    </div>
                </div>
            </div>
        </div>
        <div className="profile__main-info">
            <div className="row gx-3">
                
                <div className="col-md-3 col-sm-6">
                    <div  className="profile__main-info-item">
                    <div className="profile__main-info-icon">
                        <span>
                            <span className="profile-icon-count profile-order">
                            {
                                orders && orders.length > 0 ? orders.length : 0
                            }
                            </span>
                            
                            <i className="fa-light fa-bags-shopping fa-2xl"></i>
                        </span>
                    </div>
                    <h4 className="profile__main-info-title">Orders</h4>
                    </div>
                </div>
                <div className="col-md-3 col-sm-6">
                    <div className="profile__main-info-item">
                    <div className="profile__main-info-icon">
                        <span className="wish">
                            <span className="profile-icon-count profile-wishlist">{wishListItems.length}</span>
                            {/* <i className="fa-light fa-star"></i> */}
                            <i className="fa-light fa-star fa-2xl"></i>
                        </span>
                    </div>
                    <h4 className="profile__main-info-title">Wishlist</h4>
                    </div>
                </div>
                <div className="col-md-3 col-sm-6">
                    <div className="profile__main-info-item">
                    <div className="profile__main-info-icon">
                        <span>
                            <span className="profile-icon-count profile-wishlist">07</span>
                            <i className="fa-light fa-gift fa-2xl"></i>
                        </span>
                    </div>
                    <h4 className="profile__main-info-title">Gift Box</h4>
                    </div>
                </div>
            </div>
        </div>
        </div>
    </div>
  )
}

export default MainInfo