import { memo, useState } from 'react';
import { useDispatch } from "react-redux"
import { addToCartData } from "../../redux/actions/cartActions"
import { addWishList } from "../../redux/actions/wishListActions"
import { Link } from "react-router-dom"
import Toast from '../Toast';
const datas = [
    {name: 'A' , stats: "Active", bg: "bg-primary"},
    {name: 'I' , stats: "InActive", bg: "bg-secondary"},
    {name: 'L' , stats: "Latest", bg: "bg-success"}, 
    {name: 'T' , stats: "Trending",bg: "bg-primary"}, 
    {name: 'H' , stats: "Hot",bg: "bg-danger"}, 
    {name: 'S' , stats: "Sale",bg: "bg-danger"}, 
    {name: 'F' , stats: "Featured", bg: "bg-warning"}, 
    {name: 'N' , stats: "New",bg: "bg-primary"}, 
    {name: 'O' , stats: "Top Seller", bg: "bg-primary"},
    {name: 'R' , stats: "Top Rated", bg: "bg-info"}
  ]
const ProductCard = ({id, stat, cat, productname, numReview, price, img, rating, countInStock}) => {
    const [toasted, setToasted] = useState(null)
    const [stats, setStats] = useState(null)
    const dispatch = useDispatch()
   
    const addToCarts = (id, name, img, price, countInStock, qty) => {
        if(countInStock > 0 ){
            dispatch(addToCartData(id, name, img, price, countInStock, qty))
        } 
    }
    const addtoWish = (id, name, img, price, countInStock, qty) => {
        if(countInStock > 0 ){
        dispatch(addWishList(id, name, img, price, countInStock, qty))
    } 
    }
    const showCart = (id,st) => {
        setToasted(id)
        setStats(st)
        setTimeout(()=> {
            setToasted(null)
            setStats(null)
        }, 2000)
    }
  return (
    <div className="col-xl-3 col-lg-3 col-sm-6">
        <div className="tp-product-item p-relative transition-3 mb-25">
            <div className="tp-product-thumb p-relative fix m-img">
                
                    <Link to={`/product-details/${id}`}>
                    <img src={img} alt={img}/>
                    </Link>
              

                
                <div className="tp-product-badge">
                
                    <span className={`product badge ${datas.find(data => data.name === stat).bg}`}>
                    {datas.find(data => data.name === stat).stats}
                    </span>
                </div>

                
                <div className="tp-product-action">
                    <div className="tp-product-action-item d-flex flex-column">
                    <button type="button" className="tp-product-action-btn tp-product-add-cart-btn" 
                    id="liveToastBtn"
                    onClick={()=> {
                        addToCarts(id, productname, img,price, countInStock, 1);
                        showCart(id, "addtocart");
                    }}
                    >
                    <i className="fa-light fa-cart-shopping"></i>
                            
                        <span className="tp-product-tooltip">Add to Cart</span>
                    </button>
                    <button type="button" className="tp-product-action-btn tp-product-quick-view-btn" data-bs-toggle="modal" data-bs-target="#producQuickViewModal">
                    <i className="fa-sharp fa-regular fa-eye"></i>
                            
                            <span className="tp-product-tooltip">Quick View</span>
                    </button>
                    <button type="button" className="tp-product-action-btn tp-product-add-to-wishlist-btn"
                    onClick={()=> {
                        addtoWish(id, productname, img,price, countInStock, 1)
                        showCart(id, "addtowish");
                    }}
                    >
                    <i className="fa-light fa-star"></i>
                            
                        <span className="tp-product-tooltip">Add To Wishlist</span>
                    </button>
                    </div>
                </div>
            </div>
            {/* <!-- product content --> */}
            <div className="tp-product-content">
          
                <div className="tp-product-category">
                {toasted === id && stats=== "addtocart" && countInStock > 0 && <Toast message={"Added to Cart!"} color={"#0992fb"}/>}
                {toasted === id && stats=== "addtowish" && countInStock > 0 && <Toast message={"Added to WishList!"} color={"#65e970"}/>}
                {toasted === id && stats=== "addtowish" && countInStock ===0 && <Toast message={"Out of Stock!"} color={"#d41a1a"} />}
                {toasted === id && stats=== "addtocart" && countInStock ===0 && <Toast message={"Out of Stock!"} color={"#d41a1a"} />}
                    <a href="shop.html">{cat}</a>
                </div>
                <h3 className="tp-product-title">
                <Link to={`/product-details/${id}`}>{productname}</Link>
           
                </h3>
                <div className="tp-product-rating d-flex align-items-center">
                    <div className="tp-product-rating-icon">
                    <span><i className="fa-solid fa-star"></i></span>
                    <span><i className={`fa-star fa-${rating >=2 ? 'solid' : 'regular'}`}></i></span>
                    <span><i className={`fa-star fa-${rating >=3 ? 'solid' : 'regular'}`}></i></span>
                    <span><i className={`fa-star fa-${rating >=4 ? 'solid' : 'regular'}`}></i></span>
                    <span><i className={`fa-star fa-${rating >=5 ? 'solid' : 'regular'}`}></i></span>
                  
                    {/* <span><i className="fa-star fa-regular"></i></span> */}
                    {/* <span><i className="fa-solid fa-star-half-stroke"></i></span> */}
                    </div>
                    <div className="tp-product-rating-text">
                    <span>({numReview} Review)</span>
                    </div>
                </div>
                <div className="tp-product-price-wrapper">
                    {/* <span className="tp-product-price old-price">$320.00</span> */}
                    <span className="tp-product-price new-price">${price}</span>
                </div>
            </div>
        </div>
        </div>
  )
}

export default memo(ProductCard)