import { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { listProducts } from '../../redux/actions/productActions'

import { addToCartData } from '../../redux/actions/cartActions';

import ProductCard from './ProductCard';
import Toast from '../Toast';

const df = 'https://jhurss.pythonanywhere.com/api/products/genericslist'

const ProductArea = () => {
   const [apiData, setApiData ] = useState(df)
   const dispatch = useDispatch()

   const productList = useSelector(state => state.productList)
   const productListSearch = useSelector(state => state.productSearch)
   const { loading: searchLoading, tracker, products: productsResult} = productListSearch


   // const cartList = useSelector(state => state.addToCart)
   const { error, loading, products } = productList

   // '/?page='
   useEffect(()=> {
      dispatch(listProducts(apiData))

   }, [dispatch, apiData])


   if (loading) {
  
      return <div className='loader-container'><div className="loader"></div></div>
   }
   if (error) {
      return <div>error</div>
   }
   
  
   // let rs = products.results
   // console.log(rs.length)
   const checkPrev = (dt) => {
      if (dt !== null) {
         setApiData(dt)
         
      }
     
   }
   if(productsResult !== undefined){
      if (productsResult.length > 0){
         console.log('productsResult=>',productsResult)
      } else{
         console.log('no result')
      }
   }
   // git remote add origin https://github.com/jhurs16/ecommerce-react.git
  return (
    <section className="tp-product-area pb-55 position-relative">
    <div className="container">
       <div className="row align-items-end">
          <div className="col-xl-5 col-lg-6 col-md-5">
             <div className="tp-section-title-wrapper mb-40">
                <h3 className="tp-section-title">Our Products 
               
                   {/* <svg width="114" height="35" viewBox="0 0 114 35" fill="none" xmlns="http://www.w3.org/2000/svg">
                   <path d="M112 23.275C1.84952 -10.6834 -7.36586 1.48086 7.50443 32.9053" stroke="currentColor" stroke-width="4" stroke-miterlimit="3.8637" stroke-linecap="round"/>
                   </svg> */}
                </h3>
             </div>
          </div>
          <div className="col-xl-7 col-lg-6 col-md-7">
             <div className="tp-product-tab tp-product-tab-border mb-45 tp-tab d-flex justify-content-md-end">
                <ul className="nav nav-tabs justify-content-sm-end" id="productTab" role="tablist">
                   
                  
                 
                </ul>                         
             </div>
          </div>
       </div>
       <div className="row">
          <div className="col-xl-12">
             <div className="tp-product-tab-content">
                <div className="tab-content" id="myTabContent">
                   <div className="tab-pane fade show active" id="new-tab-pane" role="tabpanel" aria-labelledby="new-tab" >
                      <div className="row">
                         
         
                      
                      
                           {
                              loading
                              ? <div className='loader-container'><div className="loader"></div></div>
                              : error
                                  ? <div>error</div>
                                 : productsResult !== undefined && tracker === true
                                 ? productsResult !== undefined && productsResult.map((data) => (
                                    <ProductCard
                                    key={data._id}
                                    id={data._id}
                                    cat={data.category.name} 
                                    productname={data.name} 
                                    numReview={data.numReviews}
                                    price={data.price}
                                    img={data.image}
                                    rating={data.rating}
                                    countInStock={data.countInStock}
                                    stat={data.prod_stat}
                                    />
                                  ))
                                  : productsResult !== undefined && tracker === true && productsResult.length === 0
                                  ? <div>No Results</div>
                                  : products.results !== undefined && products.results.map((data) => (
                                    <ProductCard
                                    key={data._id}
                                    id={data._id}
                                    cat={data.category.name} 
                                    productname={data.name} 
                                    numReview={data.numReviews}
                                    price={data.price}
                                    img={data.image}
                                    rating={data.rating}
                                    countInStock={data.countInStock}
                                    stat={data.prod_stat}
                                    />
                                  ))
                           }
                     
                         {/* end product card */}
                     {
                       products.previous !== null && products.next !== null && <div className="tp-shop-pagination mt-20">
                           <div className="tp-pagination">
                              <nav>
                                 <ul>
                                    {/* <li>
                                       
                                       <span className={`tp-pagination-prev prev page-numbers`}  style={{cursor: "pointer", display:  products !== undefined && products.previous === null ? "none" : "block"}} onClick={()=> {
                                          products !== undefined && checkPrev(products.previous)
                                       }}>
                                       <i className="fa-light fa-arrow-left"></i>
                                       </span>
                                    </li> */}
                                    <li>
                                    
                                    <span className="page-number " style={{cursor: "pointer"}}
                                             onClick={()=> {
                                                products !== undefined && checkPrev(products.previous)
                                             }}
                                          >Prev</span>
                                    
                                    </li>
                                    <li>
                                    
                                    <span className="page-number " style={{cursor: "pointer"}}
                                             onClick={()=> {
                                                products !== undefined && checkPrev(products.next)
                                             }}
                                          >Next</span>
                                    
                                    </li>
                                    <span>
                                    
                                    </span>
                                    {/* <li>
                                       <span className="next page-numbers" style={{cursor: "pointer",display:  products !== undefined && products.next === null ? "none" : "block"}}
                                       onClick={()=> {
                                          products !== undefined && checkPrev(products.next)
                                       }}
                                       >
                                       <i className="fa-light fa-arrow-right"></i>
                                       </span>
                                      
                                    </li> */}
                                 </ul>
                               </nav>
                           </div>
                        </div>
                     }
                      </div>
                   </div>
                 
                 </div>                         
             </div>
          </div>
       </div>
       
    </div>
 </section>
  )
}

export default ProductArea