// import { useState, useRef, useEffect } from "react";
import { memo } from 'react';
import { useDispatch, useSelector } from "react-redux";
import { addCategory } from "../../redux/actions/categoryActions";

const ShopCat = ({cat, uniqueChars}) => {
    const dispatch = useDispatch()
    // const xr = uniqueChars.map((val)=> (
    //     {name: `cat_${val}`, stat: false}
    // ))
    const addToCats = (name, stat) => {
      dispatch(addCategory(name,stat))
    }




  return (
    <div className="tp-shop-widget-content">
        <div className="tp-shop-widget-checkbox">
            <ul className="filter-items filter-checkbox">
            <li className="filter-item checkbox">
                <input id={`cat_${cat}`} type="checkbox" 
                onChange={()=> addToCats(cat, event.target.checked)}
                // onClick={(event) => setAgreement({name: event.target.id, stat: event.target.checked})}
                // onChange={}
                />
                <label htmlFor={`cat_${cat}`}>{cat}</label>
            </li>
            
            </ul>
            {/* <!-- .filter-items --> */}
        </div>
    </div>
  )
}

export default memo(ShopCat)