import axios from 'axios'
import {
    CAT_FILTER_ADD,
    CAT_FILTER_REMOVE,
    CAT_LIST_REQUEST,
    CAT_LIST_SUCCESS,
    CAT_LIST_FAIL
} from '../../constants/categoryConstants.js'

export const addCategory = (name, stat) => (dispatch) =>{
    dispatch({
        type: CAT_FILTER_ADD,
        payload: {
            name: name,
            stat: stat,
      
        }
    })

}

export const removeFromCatList = (id, name) => (dispatch) => {
    dispatch({
        type: CAT_FILTER_REMOVE,
        payload: {
            name: name,
        }
    })

}


export const listCategories = () => async (dispatch)=> {
    try {
        dispatch({type: CAT_LIST_REQUEST})
        const { data } = await axios.get('https://jhurss.pythonanywhere.com/api/categories/')
        dispatch({
            type: CAT_LIST_SUCCESS,
            payload: data
        })
    } catch (error) {
        dispatch({
            type: CAT_LIST_FAIL,
            payload: error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
        })
    }
}