import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector} from 'react-redux'
import { saveShippingAddress } from '../redux/actions/cartActions';
import CheckOutSteps from '../components/CheckOutSteps';

const Shipping = () => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const cart = useSelector(state => state.cart)
    const { shippingAddress } = cart
    const [address, setAddress ] = useState(shippingAddress.address)
    const [city, setCity ] = useState(shippingAddress.city)
    const [postalCode, setPostalCode ] = useState(shippingAddress.postalCode)
    const [country, setCountry ] = useState(shippingAddress.country)
    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin
    useEffect(()=>{
        if(!userInfo){
            navigate("/login")
        }
    },[])
    const handleSubmit = (e) => {
        e.preventDefault()
        dispatch(saveShippingAddress({address, city, postalCode, country}))
        navigate("/payment")
    }
  return (
    <section className="tp-contact-area pb-100">
        <div className="container mx-auto">
        <div className="tp-contact-inner">
            <div className="row justify-content-center">

                <div className="col-xl-8 col-lg-7">
                    <CheckOutSteps step1 step2/>
                    <div className="tp-contact-wrapper">
                    <h3 className="tp-contact-title">Shipping</h3>

                    <div className="tp-contact-form">
                        <form onSubmit={handleSubmit}>
                            <div className="tp-contact-input-wrapper">
                                <div className="tp-contact-input-box">
                                <div className="tp-contact-input">
                                    <input name="name" id="name" type="text" placeholder="Write your address" required
                                    value={address ? address : ''}
                                    onChange={(e)=> setAddress(e.target.value)}
                                    />
                                </div>
                                <div className="tp-contact-input-title">
                                    <label htmlFor="name">Address</label>
                                </div>
                                </div>
                                <div className="tp-contact-input-box">
                                <div className="tp-contact-input">
                                    <input name="text"  type="text" placeholder="Write your city" required
                                    value={city ? city : ''}
                                    onChange={(e)=> setCity(e.target.value)}
                                    />
                                </div>
                                <div className="tp-contact-input-title">
                                    <label htmlFor="email">City</label>
                                </div>
                                </div>
                                <div className="tp-contact-input-box">
                                <div className="tp-contact-input">
                                    <input name="subject" id="subject" type="text" placeholder="Write your postal code" required
                                    value={postalCode ? postalCode : ''}
                                    onChange={(e)=> setPostalCode(e.target.value)}
                                    />
                                </div>
                                <div className="tp-contact-input-title">
                                    <label htmlFor="subject">Postal Code</label>
                                </div>
                                </div>
                                <div className="tp-contact-input-box">
                                <div className="tp-contact-input">
                                    <input name="subject" id="subject" type="text" placeholder="Write your country" required
                                    value={country ? country : ''}
                                    onChange={(e)=> setCountry(e.target.value)}
                                    />
                                </div>
                                <div className="tp-contact-input-title">
                                    <label htmlFor="subject">Country</label>
                                </div>
                                </div>
                                {/* <div className="tp-contact-input-box">
                                <div className="tp-contact-input">
                                    <textarea id="message" name="message" placeholder="Write your message here..."></textarea>
                                </div>
                                <div className="tp-contact-input-title">
                                    <label htmlFor="message">Your Message</label>
                                </div>
                                </div> */}
                            </div>
                            {/* <div className="tp-contact-suggetions mb-20">
                                <div className="tp-contact-remeber">
                                <input id="remeber" type="checkbox"/>
                                <label htmlFor="remeber">Save my name, email, and website in this browser for the next time I comment.</label>
                                </div>
                            </div> */}
                            <div className="tp-contact-btn">
                                <button type="submit">Submit</button>
                            </div>
                        </form>
                        <p className="ajax-response"></p>
                    </div>
                    </div>
                </div>
               
            </div>
        </div>
        </div>
    </section>
  )
}

export default Shipping