import { Link } from "react-router-dom"
import { useDispatch, useSelector } from "react-redux"
import { removeFromCart, addToCartData } from "../../redux/actions/cartActions"

const CartPage = () => {

    const cart = useSelector(state => state.cart)
    const dispatch = useDispatch()
    const { cartItems } = cart
    const removeProduct = (id) => {
        dispatch(removeFromCart(id))
    }

  return (
    <div>

        <section className="breadcrumb__area include-bg pt-95 pb-50">
            <div className="container">
            <div className="row">
                <div className="col-xxl-12">
                    <div className="breadcrumb__content p-relative z-index-1">
                        <h3 className="breadcrumb__title">Shopping Cart</h3>
                        <div className="breadcrumb__list">
                        <span><Link  to={`/`}>Home</Link></span>
                        
                        <span>Shopping Cart</span>
                        </div>
                    </div>
                </div>
            </div>
            </div>
        </section>
        <section className="tp-cart-area pb-120">
            <div className="container">
               <div className="row">
                  <div className="col-xl-9 col-lg-8">
                     <div className="tp-cart-list mb-25 mr-30">
                        <table className="table">
                           <thead>
                             <tr>
                               <th colSpan="2" className="tp-cart-header-product">Product</th>
                               <th className="tp-cart-header-price">Price</th>
                               <th className="tp-cart-header-quantity">Quantity</th>
                               <th className="tp-cart-header-quantity">SubTotal</th>
                               <th></th>
                             </tr>
                           </thead>
                           <tbody>
                            {
                                cartItems.map((data) => (
                                     <tr key={data.product_id}>
                                
                                 <td className="tp-cart-img"><a href="product-details.html"> 
                                 <img src={data.image} alt=""/></a></td>
                                 
                                 <td className="tp-cart-title"><a href="product-details.html" rel="noreferrer" >
                                    {data.name}</a></td>
                                
                                 <td className="tp-cart-price"><span>${data.price}</span></td>
                                
                                 <td className="tp-cart-quantity">
                                    <div className="tp-product-quantity mt-10 mb-10">
                                       {/* <span className="tp-cart-minus">
                                       <i className="fa-solid fa-minus"></i>
                                                                               
                                       </span>
                                       <input className="tp-cart-input" type="text" value="1" onChange={()=> ''}/>
                                       <span className="tp-cart-plus">
                                       <i className="fa-solid fa-plus"></i>
                                     
                                       </span> */}
                                       <select className="form-select" aria-label="Default select example"
                                          value={data.qty}
                                          // onChange={(e) => dispatch(addToCart(item.product, Number(e.target.value)))}
                                          onChange={(e)=> 
                                             dispatch(addToCartData(data.product_id, data.name, data.image, data.price, data.countInStock, Number(e.target.value)))
                                          }
                                       
                                       >
                                      
                                       {

                                          [...Array(data.countInStock).keys()].map((x) => (
                                             <option key={x + 1} value={x + 1}>
                                                {x + 1}
                                             </option>
                                          ))
                                       }
                                        </select>
                                    </div>
                                 </td>
                                 <td className="tp-cart-action">
                                    ${data.qty * data.price}
                                 </td>
                                 <td className="tp-cart-action">
                                    <button className="tp-cart-action-btn" onClick={()=> removeProduct(data.product_id)}>
                                    <i className="fa-solid fa-xmark"></i>
                                      
                                       <span> Remove</span>
                                    </button>
                                 </td>
                              </tr>
                                ))
                            }
                             
                            
                             
                            
                           </tbody>
                         </table>
                     </div>
                     <div className="tp-cart-bottom">
                        <div className="row align-items-end">
                           {/* <div className="col-xl-6 col-md-8">
                              <div className="tp-cart-coupon">
                                 <form action="#">
                                    <div className="tp-cart-coupon-input-box">
                                       <label>Coupon Code:</label>
                                       <div className="tp-cart-coupon-input d-flex align-items-center">
                                          <input type="text" placeholder="Enter Coupon Code"/>
                                          <button type="submit">Apply</button>
                                       </div>
                                    </div>
                                 </form>
                              </div>
                           </div> */}
                           {/* <div className="col-xl-6 col-md-4">
                              <div className="tp-cart-update text-md-end">
                                 <button type="button" className="tp-cart-update-btn">Update Cart</button>
                              </div>
                           </div> */}
                        </div>
                     </div>
                  </div>
                  <div className="col-xl-3 col-lg-4 col-md-6">
                     <div className="tp-cart-checkout-wrapper">
                        <div className="tp-cart-checkout-top d-flex align-items-center justify-content-between">
                           <span className="tp-cart-checkout-top-title">Items</span>
                           <span className="tp-cart-checkout-top-price">{cartItems.reduce((acc,item)=> acc + item.qty, 0)} items</span>
                        </div>
                        
                        <div className="tp-cart-checkout-total d-flex align-items-center justify-content-between">
                           <span>Total</span>
                           <span>${cartItems.reduce((acc,item)=> acc + item.qty * item.price, 0)}</span>
                        </div>
                        {cartItems.length > 0 ? <div className="tp-cart-checkout-proceed">
                           <Link to="/shipping" className="tp-cart-checkout-btn w-100">
                           Proceed to Checkout
                           </Link>
                           
                        </div> :

                           <div className="tp-cart-checkout-proceed">
                           <button className="btn btn-secondary disabled tp-cart-checkout-btn w-100">
                           Proceed to Checkout
                           </button>

                           </div>
                         }
                        
                        
                     </div>
                  </div>
               </div>
            </div>
         </section>
    </div>
  )
}

export default CartPage