import { useSelector } from "react-redux"
import LoaderComp from "../../LoaderComp"
import { Link } from "react-router-dom";
const MyOrders = () => {
    const convertDate = (data) => {
        const datetime = new Date(data);
        const options = { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric', second: 'numeric', hour12: false };
        const readableDatetime = datetime.toLocaleString('en-US', options);
        return readableDatetime;
    }
    const orderListMy = useSelector(state => state.orderListMy)
    const { loading, error, orders } = orderListMy
  return (
    <div >
        <div className="profile__ticket table-responsive">
        <table className="table">
            <thead>
                <tr>
                    <th scope="col">Order Id</th>
                   
                    <th scope="col">Order Date</th>
                    <th scope="col">Total Price</th>
                    <th scope="col">Paid</th>
                    <th scope="col">View</th>
                </tr>
            </thead>
            <tbody>
                {
                    loading ? <LoaderComp/>
                    : error ? <div>Error</div>
                    :
                
                    orders.map((order) => (
                        <tr key={order._id}>
                        <th scope="row"> {order._id}</th>
                    
                        <td data-info="title">{order.createdAt.substr(0,10)}</td>
                        {/* {convertDate(order.createdAt)} */}
                        <td data-info="status pending">${order.totalPrice} </td>
                        <td data-info="status pending">{
                        order.isPaid ? <i className="fa-solid fa-circle-check" style={{color: "#55fd49"}}></i>
                        : <i className="fa-solid fa-circle-xmark" style={{color: "#bd0505"}}></i>
                        
                        } </td>
                        <td>
                            <Link to={`/order/${order._id}`} className="border p-2 bg-primary text-white">
                                details
                            </Link>
                        </td>

                    </tr>
                    ))
                }
                {/* <tr>
                    <th scope="row"> #2121</th>
                    <td data-info="title">How to contact</td>
                    <td data-info="status done">Resolved</td>
                    <td><a href="#" className="tp-logout-btn">Invoice</a></td>
                </tr> */}
            </tbody>
        </table>
        </div>
    </div>
  )
}

export default MyOrders