import { Link } from "react-router-dom"
import { useSelector } from "react-redux"
const MobileMenuSticky = ({closeBtnSearch}) => {
   const userLogin = useSelector(state => state.userLogin)
   const { userInfo } = userLogin
  return (
    <div id="tp-bottom-menu-sticky" className="tp-mobile-menu d-lg-none">
         <div className="container">
            <div className="row row-cols-5 d-flex justify-content-around">
               <div className="col">
                  <div className="tp-mobile-item text-center">
             x
                        <Link to="/" className="tp-mobile-item-btn">
                        <i className="flaticon-store"></i>
                        <span>Store</span>
                        </Link>
      
                  </div>
               </div>
               {/* <div className="col">
                  <div className="tp-mobile-item text-center">
                     <button className="tp-mobile-item-btn tp-search-open-btn"
                     onClick={closeBtnSearch}
                     >
                        <i className="flaticon-search-1"></i>
                        <span>Search</span>
                     </button>
                  </div>
               </div> */}
               <div className="col">
                  <div className="tp-mobile-item text-center">
                 
                        <Link to="/wish-list" className="tp-mobile-item-btn">
                           <i className="flaticon-love"></i>
                           <span>Wishlist</span>
                        </Link>
                  
                  </div>
               </div>
               <div className="col">
                  <div className="tp-mobile-item text-center">
              
                        <Link to={userInfo ? "/profile" : "/login"} className="tp-mobile-item-btn">
                        <i className="flaticon-user"></i>
                        <span>Account</span>
                        </Link>
                        
              
                  </div>
               </div>
               <div className="col">
                  <div className="tp-mobile-item text-center">
                     <button className="tp-mobile-item-btn tp-offcanvas-open-btn">
                        <i className="flaticon-menu-1"></i>
                        <span>Menu</span>
                     </button>
                  </div>
               </div>
            </div>
         </div>
      </div>
  )
}

export default MobileMenuSticky