// import { useState, useRef, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { addProductStatus } from "../../redux/actions/productStatusActions";

const ShopStatus = ({stat}) => {
    const dispatch = useDispatch()
    // const xr = uniqueChars.map((val)=> (
    //     {name: `cat_${val}`, stat: false}
    // ))
    const addToProdStats = (name, stat) => {
      dispatch(addProductStatus(name,stat))
    }
    const datas = [
      {name: 'A' , stats: "Active"},
      {name: 'I' , stats: "InActive"},
      {name: 'L' , stats: "Latest"}, 
      {name: 'T' , stats: "Trending"}, 
      {name: 'H' , stats: "Hot"}, 
      {name: 'S' , stats: "Sale"}, 
      {name: 'F' , stats: "Featured"}, 
      {name: 'N' , stats: "New"}, 
      {name: 'O' , stats: "Top Seller"},
      {name: 'R' , stats: "Top Rated"}
    ]
    


  return (
    <div className="tp-shop-widget-content">
        <div className="tp-shop-widget-checkbox">
            <ul className="filter-items filter-checkbox">
              <li className="filter-item checkbox">
                  <input id={`on_${stat}`} type="checkbox"
                  onChange={()=> addToProdStats(stat, event.target.checked)}
                  />
                  <label htmlFor={`on_${stat}`}>{datas.find(data => data.name === stat).stats}</label>
              </li>
              
            </ul>
            {/* <!-- .filter-items --> */}
        </div>
      </div>
  )
}

export default ShopStatus;