import {
    PRODUCT_STATUS_FILTER_ADD
} from '../../constants/productStatusConstants.js'
export const productStatusReducer = (state = {productStatusList: []}, action) => {
    switch (action.type) {
      
        case PRODUCT_STATUS_FILTER_ADD: {
            const item = action.payload //item that click
            const existItem = state.productStatusList.find(x => x.name === item.name) 
   
            if (existItem ) {
             
                return {
                    
                    ...state,
                    productStatusList: state.productStatusList.map(x =>
                        x.name === existItem.name ? item : x)
                }

            } else {
                return {
                    ...state,
                    productStatusList: [...state.productStatusList, item]
                }
            }
        }
        

        default:
            return state
    }
}