import {useState} from 'react';

const SearchArea = ({refSearch, closeBtnSearch}) => {
   const handleSubmit = (e) => {
      e.preventDefault()
   }
  return (
    <section className="tp-search-area" ref={refSearch}>
         <div className="container">
            <div className="row">
               <div className="col-xl-12">
                  <div className="tp-search-form">
                     <div className="tp-search-close text-center mb-20">
                        <button className="tp-search-close-btn tp-search-close-btn"
                        onClick={closeBtnSearch}
                        ></button>
                     </div>
                     <form onSubmit={handleSubmit}>
                        <div className="tp-search-input mb-10">
                           <input type="text" placeholder="Search for product..."
                           
                           />
                           <button type="submit"><i className="flaticon-search-1"></i></button>
                        </div>
                     
                     </form>
                  </div>
               </div>
            </div>
         </div>
      </section>
  )
}

export default SearchArea