import { useSelector, useDispatch } from "react-redux"
import { Link } from 'react-router-dom'
import { removeFromCart } from "../redux/actions/cartActions"
const CartMini = ({refCheck, checkBtn}) => {
    const cart = useSelector(state => state.cart)
    const dispatch = useDispatch()
    const { cartItems } = cart
    // console.log('carts=',cartItems)
    const removeProduct =  (id) => {
        dispatch(removeFromCart(id))
    }
  return (
    <div className="cartmini__area tp-all-font-roboto" ref={refCheck}>
         <div className="cartmini__wrapper d-flex justify-content-between flex-column">
             <div className="cartmini__top-wrapper">
                 <div className="cartmini__top p-relative">
                     <div className="cartmini__top-title">
                         <h4>Shopping cart</h4>
                     </div>
                     <div className="cartmini__close">
                         <button type="button" className="cartmini__close-btn cartmini-close-btn" onClick={checkBtn}><i className="fal fa-times"></i></button>
                     </div>
                 </div>
                 <div className="cartmini__shipping">
                  <p> Free Shipping for all orders over <span>$50</span></p>
                  <div className="progress">
                     <div className="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" data-width="70%" aria-valuenow="70" aria-valuemin="0" aria-valuemax="100"></div>
                   </div>                   
                 </div>
                 <div className="cartmini__widget">
                    {
                        cartItems.map((data)=> (
                            <div className="cartmini__widget-item" key={data.product_id}>
                                <div className="cartmini__thumb">
                                <a href="product-details.html">
                                    <img src={data.image} alt=""/>
                                </a>
                                </div>
                                <div className="cartmini__content">
                                <h5 className="cartmini__title"><a href="product-details.html">{data.name}</a></h5>
                                <div className="cartmini__price-wrapper">
                                    <span className="cartmini__price">${data.price}</span>
                                    {/* <span className="cartmini__quantity">x2</span> */}
                                </div>
                                </div>
                                {/* <a href="#" className="cartmini__del"><i className="fa-light fa-x"></i></a> */}
                                <button className="cartmini__del" onClick={()=> removeProduct(data.product_id)}><i className="fa-light fa-x"></i></button>
                            </div>
                        ))
                    }
                     
                 </div>
           
                 <div className="cartmini__empty text-center d-none">
                     {/* <img src="assets/img/product/cartmini/empty-cart.png" alt=""/> */}
                     <p>Your Cart is empty</p>
                     <a href="shop.html" className="tp-btn">Go to Shop</a>
                 </div>
             </div>
             <div className="cartmini__checkout">
                 {/* <div className="cartmini__checkout-title mb-30">
                     <h4>Subtotal:</h4>
                     <span>$113.00</span>
                 </div> */}
                 <div className="cartmini__checkout-btn">
                     {/* <a href="cart.html" className="tp-btn mb-10 w-100"> view cart</a> */}
                     <Link className="tp-btn mb-10 w-100" to={`/cart`}>view cart</Link>
                     {/* <a href="checkout.html" className="tp-btn tp-btn-border w-100"> checkout</a> */}
                 </div>
             </div>
         </div>
     </div>
  )
}

export default CartMini