import {
    CAT_FILTER_ADD,
    CAT_FILTER_REMOVE,
    CAT_LIST_FAIL,
    CAT_LIST_REQUEST,
    CAT_LIST_SUCCESS
} from '../../constants/categoryConstants.js'
export const categoryReducer = (state = {catList: []}, action) => {
    switch (action.type) {
      
        case CAT_FILTER_ADD: {
            const item = action.payload //item that click
            const existItem = state.catList.find(x => x.name === item.name) 
            //existing data in the local storage
            // const item = action.payload
           
            // if(item.stat === true){
            //     return ''
            // } else {
            //     return {
            //         ...state,
            //         catList: [...state.catList, item]
            //     }
            // }
            if (existItem ) {
             
                return {
                    
                    ...state,
                    catList: state.catList.map(x =>
                        x.name === existItem.name ? item : x)
                }

            } else {
                return {
                    ...state,
                    catList: [...state.catList, item]
                }
            }
        }
        case CAT_FILTER_REMOVE: 
            return {
                ...state,
                catList: state.catList.filter(x => x.name !== action.payload.name)
            }

        

        default:
            return state
    }
}

export const categoryListReducer = ( 
    state = {categories: []},
    action
) => {
    switch(action.type) {
        case CAT_LIST_REQUEST:
            return {loading: true, categories: []}
        case CAT_LIST_SUCCESS:
            return {loading: false, categories: action.payload}

        case CAT_LIST_FAIL:
            return {loading: false, error: action.payload}
        default:
            return state
    }
}