

// import CartMini from "./CartMini"
import Header from "./Header"
import HeaderSticky from "./HeaderSticky"

import Banner from "./main/Banner"
import Feature from "./main/Feature"

import MainSlider from "./main/MainSlider"
import ProductArea from "./main/ProductArea"
import ProductBanner from "./main/ProductBanner"
import ProductCategory from "./main/ProductCategory"
import ProductModal from "./main/ProductModal"

const Home = () => {

  
  return (
    <div>
        
        
        

      <HeaderSticky/>
    <main>
        
    
        {/* <!-- slider area start --> */}
            <MainSlider/>
         {/* <!-- slider area end --> */}

        {/* <!-- product category area start --> */}
            <ProductCategory/>
        {/* <!-- product category area end --> */}


        {/* <!-- feature area start --> */}
        <Feature/>
        {/* <!-- feature area end --> */}

        {/* <!-- product area start --> */}
            <ProductArea/>
        {/* <!-- product area end --> */}

        {/* <!-- banner area start --> */}
        {/* <Banner/> */}
         {/* <!-- banner area end --> */}

         {/* <!-- product banner area start --> */}
         {/* <ProductBanner/> */}
         {/* <!-- product banner area end --> */}
        <ProductModal/>
         
         
    </main>

    
    </div>
  )
}

export default Home