import {  useEffect, memo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { listCategories } from '../../redux/actions/categoryActions';
import LoaderComp from '../LoaderComp'
const ProductCategory = () => {
   const dispatch = useDispatch()

   const categoryList = useSelector(state => state.categoryList)

   const { error, loading, categories } = categoryList
   
   useEffect(()=> {
      dispatch(listCategories())
   }, [dispatch])


   // console.log(products)
      if (loading) {
    
         return <LoaderComp/>
      }
      if (error) {
         return <div>error</div>
      }
  return (
    <section className="tp-product-category pt-60 pb-15">
            <div className="container">
               <div className="row row-cols-xl-5 row-cols-lg-5 row-cols-md-4 ">
                  {
               loading
               ? <LoaderComp/>
               : error
                     ? <div>error</div>
                     : categories.map((data) => (
                    
                     
                  <div className="col" key={data.id}>
                     <div className="tp-product-category-item text-center mb-40">
                        <div className="tp-product-category-thumb fix">
                           
                              <img className='img-fluid' src={data.image} alt={data.image} />
                          
                        </div>
                        <div className="tp-product-category-content">
                           
                              <span>{data.name}</span>
                           
                           <p>{data.prod_num} {data.prod_num > 1 ? 'Products': 'Product'}</p>
                        </div>
                     </div>
                  </div>
                  ))
               }
               </div>
            </div>
         </section>
  )
}

export default memo(ProductCategory)