import {
    WISHLIST_ADD_ITEM,
    WISHLIST_REMOVE_ITEM
} from '../../constants/wishListConstants'

export const wishListReducer = (state = {wishListItems: JSON.parse(localStorage.getItem('wishListItems')) ?? []}, action) => {
    switch (action.type) {
      
        case WISHLIST_ADD_ITEM: {
            const item = action.payload
            const existItem = state.wishListItems.find(x => x.product_id === item.product_id)
            // const item = action.payload
            if (existItem) {
                return {
                    ...state,
                    wishListItems: state.wishListItems.map(x =>
                        x.product_id === existItem.product_id ? item : x)
                }

            } else {
                return {
                    ...state,
                    wishListItems: [...state.wishListItems, item]
                }
            }
        }
        case WISHLIST_REMOVE_ITEM:
            return {
                ...state,
                wishListItems: state.wishListItems.filter(x => x.product_id !== action.payload)
            }

        

        default:
            return state
    }
}