import { useState, useEffect } from 'react'

import { Link, useParams, useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import PayP from '../components/lib/Paypal-logo.png'
import { getOrderDetails, payOrder, deliverOrder } from '../redux/actions/orderActions'
// import { ORDER_PAY_RESET, ORDER_DELIVER_RESET } from '../constants/orderConstants'
import { PayPalButtons } from "@paypal/react-paypal-js";
import { ORDER_PAY_RESET, ORDER_DELIVER_RESET } from '../constants/orderConstants'

const OrderScreen = () => {
    const {orderId} = useParams()
    const dispatch = useDispatch()
    const [sdkReady, setSdkReady ] = useState(false)
    const navigate = useNavigate()
    // const [sdkReady, setSdkReady] = useState(false)

    const orderDetails = useSelector(state => state.orderDetails)
    const { order, error, loading } = orderDetails

    const orderPay = useSelector(state => state.orderPay)
    const { loading: loadingPay, success: successPay } = orderPay

    // const orderPay = useSelector(state => state.orderPay)
    // const { loading: loadingPay, success: successPay } = orderPay

    // const orderDeliver = useSelector(state => state.orderDeliver)
    // const { loading: loadingDeliver, success: successDeliver } = orderDeliver

    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin


    if (!loading && !error) {
        order.itemsPrice = order.orderItems.reduce((acc, item) => acc + item.price * item.qty, 0).toFixed(2)
    }
    const client_id = 'ASJdIbuDns4rkTfY8ALxZen1Axg2xe-H_TdjpztHtTyvJ_DBILyxwcgdSjFuVXMsiN5TyqSRrVLuC49r'
    //https://www.paypal.com/sdk/js?client-id=test&currency=USD
    const addPayPalScript = ()=> {
        const script = document.createElement('script');
        script.type = 'text/javascript'
        script.src = `https://www.paypal.com/sdk/js?client-id=${client_id}&currency=USD`
        script.async = true
        script.onload = ()=>{
            setSdkReady(true)
        }
        document.body.appendChild(script)
    }
    useEffect(() => {

        if (!userInfo) {
            navigate('/login')
        }

        if (!order || successPay || order._id !== Number(orderId)) {
            // dispatch({ type: ORDER_PAY_RESET })
            // dispatch({ type: ORDER_DELIVER_RESET })
            dispatch({ type: ORDER_PAY_RESET })
            dispatch(getOrderDetails(orderId))
        }
         else if (!order.isPaid) {
            if (!window.paypal) {
                addPayPalScript()
            } else {
                setSdkReady(true)
            }
        }
    }, [dispatch, order, orderId, successPay])

    const successPaymentHandler = (paymentResult) => {
        dispatch(payOrder(orderId, paymentResult))
    }

  return (

    loading ? (
        <div>Loading</div>
    ) : error ? (
        <div>{error}</div>
    ) : 
    <section className="tp-contact-area pb-100">
<div className="container mx-auto">
<div className="tp-contact-inner">
    <div className="row">
      
    <h3 className="tp-checkout-bill-title">Order: {orderId}</h3>
    <div className="col-lg-8">
    
        <div className="tp-checkout-bill-area">
        <h3 className="tp-checkout-bill-title">Shipping Details</h3>

        <div className="tp-checkout-bill-form">
          
             <ul className="list-group list-group-flush">
                <li className="list-group-item">
                    <p><span className="fw-bold">Name :</span> </p>
                    <p>{order.user.name}</p>
                    
                </li>
                <li className="list-group-item">
                <p><span className="fw-bold">Email :</span> </p>
                    <p>{order.user.email}</p>
                </li>
                <li className="list-group-item">
                    <p><span className="fw-bold">Address :</span> </p>
                    <p>{order.shippingAddress.address}, {order.shippingAddress.city}
                        {' '}, {order.shippingAddress.postalCode} {' '} {order.shippingAddress.country}
                    </p>
                    <p className='mt-2 mb-0'><span className="fw-bold ">Delivery Status :</span> </p> 
                    {
                        order.isDelivered ? <div className='alert alert-success p-1'>Delivered on : <strong>{order.deliveredAt}</strong> </div>
                        : <div className='alert alert-warning mt-2 p-1'>
                            <strong>
                            Not Delivered
                            </strong>
                        </div> 
                        
                    }
                </li>
                <li className="list-group-item">
                <p><span className="fw-bold">Payment Method :</span> </p> 
                <img className='img-fluid mx-2' style={{maxWidth: "85px"}} src={PayP} alt={PayP}/>
                <p className='mt-2 mb-0'><span className="fw-bold ">Payment Status :</span> </p> 
                {
                    order.isPaid ? <div className='alert alert-success mt-2 p-1'>
                        Paid on : <strong>{
                        order.paidAt
                        
                        }</strong> </div>
                    : <div className='alert alert-warning p-1'>
                        <strong>
                        Not Paid
                        </strong>
                    </div> 
                    
                }
                
                </li>
                <li className="list-group-item px-0">
                    <h3 className="tp-checkout-bill-title">Order Summary</h3>
                    { <ul className="list-group list-group-flush">
                        {error && <li className="list-group-item">
                            <div className='alert alert-danger'>{error}</div>
                        </li>}
                        {
                            order.orderItems.map((item, index) => (
                                <li className="list-group-item" key={index}>
                                <div className="row">
                                
                                    <div className="col">
                                        <img src={item.image} alt={item.name} style={{maxWidth: "70px"}}/>
                                    </div>
                                    <div className="col d-flex align-items-center">{item.name}</div>
                                    <div className="col d-flex align-items-center">{item.qty } x ${item.price} = ${(item.qty * item.price).toFixed(2)}</div>
                            
                                </div>
                                </li>
                            ))
                        }
                    </ul>
                    }
                </li>
                
            </ul>
        </div>
        </div>
    </div>
    <div className="col-lg-4">
                    {/* <!-- checkout place order --> */}
                     <div className="tp-checkout-place white-bg">
                        <h3 className="tp-checkout-place-title">Your Order</h3>

                        <div className="tp-order-info-list">
                           <ul>

                              {/* <!-- header --> */}
                              {/* <li className="tp-order-info-list-header">
                                 <h4>Product</h4>
                                 <h4>Total</h4>
                              </li> */}

                              {/* <!-- item list --> */}
                           

                              {/* <!-- subtotal --> */}
                              <li className="tp-order-info-list-subtotal">
                                 <span>Items</span>
                                
                                 <span>{order.orderItems.reduce((acc,item)=> acc + item.qty, 0)}</span>
                              </li>
                             
                              <li className="tp-order-info-list-subtotal">
                                 <span>Subtotal</span>
                                 <span>${order.itemsPrice}</span>
                              </li>
                              <li className="tp-order-info-list-subtotal">
                                 <span>Shipping</span>
                                 <span>${order.shippingPrice}</span>
                              </li>
                              <li className="tp-order-info-list-subtotal">
                                 <span>Tax</span>
                                 <span>${order.taxPrice}</span>
                              </li>
                              <li className="tp-order-info-list-total">
                                 <span>Total</span>
                                 <span>${order.totalPrice}</span>
                              </li>
                              {/* <!-- shipping --> */}
                              {!order.isPaid && (
                                    <li className="tp-order-info-list-total">
                                        {loadingPay && <div>Loading..</div>}

                                        {!sdkReady ? (
                                            <div>Loading..</div>
                                        ) : (
                                                // <PayPalButtons
                                                //     amount={order.totalPrice}
                                                //     onSuccess={successPaymentHandler}
                                                // />
                                                <PayPalButtons
                                                    createOrder={(data, actions) => {
                                                        return actions.order.create({
                                                            purchase_units: [
                                                                {
                                                                    amount: {
                                                                        value: `${order.totalPrice}`,
                                                                    },
                                                                },
                                                            ],
                                                        });
                                                    }}
                                                    onApprove={(data, actions) => {
                                                        console.log('data=>>',data)
                                                        console.log('actions=>>',actions)
                                                        dispatch(payOrder(orderId, data))
                                                        return actions.order.capture().then((details) => {
                                                            console.log('details=>>',details)
                                                            const name = details.payer.name.given_name;
                                                            alert(`Transaction completed by ${name}`);
                                                        });
                                                    }}
                                                />
                                            )}
                                    </li>
                                )}
                              {/* <!-- total --> */}
                             
                           </ul>
                        </div>
                       
                   
                       
                       
                     </div>
                  </div>
        {/* <div className="col-xl-8 col-lg-7">
            <CheckOutSteps step1 step2 step3 step4/>

        </div> */}
       
    </div>
</div>
</div>
</section>
  )
}

export default OrderScreen